
.bills header {
  text-align: center;
  color: #fff;
  border-bottom: 1px solid #ededed;
}
.bills header.positive {
  background: #1abc9c;
}
.bills header.negative {
  background: #e74c3c;
}
.header-title {
  outline: none;
  position: relative;
  margin: 0 auto;
}
.header-title-inner {
  line-height: 20px;
  padding: 6px 0;
  position: relative;
}
.header-content {
  font-size: 25px;
  padding-top: 20px;
  position: relative;
  margin: 0 auto;
  width: 100%;
}
.bills footer {
  text-align: right;
}
.bills footer label {
  position: relative;
  display: inline-block;
}



